<template>
  <div id="contact">
    <v-row class="eact_part pt-6 mt-15">
      <v-col>
        <p class="header_text text-center font-weight-bold">
          CONTACT <span class="primary--text">US</span>
        </p>
        <v-container>
          <v-form ref="form" lazy-validation>
            <v-row justify="center" class="each_part">
              <v-col cols="12" sm="6" md="6">
                <v-text-field
                  color="primary"
                  v-model="firstname"
                  :rules="nameRules"
                  label="first name"
                  required
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="6">
                <v-text-field
                  color="primary"
                  v-model="lastname"
                  :rules="nameRules"
                  label="Last name"
                  required
                ></v-text-field>
              </v-col>
            </v-row>
            <v-row justify="center" class="each_part pt-6">
              <v-col cols="12" sm="6" md="6">
                <v-text-field
                  dense
                  color="primary"
                  v-model="email"
                  :rules="emailRules"
                  label="E-mail"
                  required
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="6">
                <v-text-field
                  dense
                  color="primary"
                  v-model="number"
                  counter="10"
                  :rules="numberRules"
                  label="Phone Number"
                  required
                ></v-text-field>
              </v-col>
            </v-row>
          </v-form>
          <v-row justify="center" class="each_part pt-6">
            <v-col>
              <v-textarea
                outlined
                counter="150"
                v-model="query"
                :rules="queryRules"
                label="your queries"
              ></v-textarea>
              <v-btn right color="primary" dark @click="validate"> Send </v-btn>
            </v-col>
          </v-row>
        </v-container>
      </v-col>
    </v-row>
  </div>
</template>
<script>
import contactUsService from "@/services/contactUsService";

export default {
  name: "ContactUs",

  data: () => ({
    valid: true,
    firstname: "",
    lastname: "",
    nameRules: [
      (v) => !!v || "Name is required",
      (v) => (v && v.length <= 10) || "Name must be less than 10 characters",
    ],
    email: "",
    emailRules: [
      (v) => !!v || "E-mail is required",
      (v) => /.+@.+\..+/.test(v) || "E-mail must be valid",
    ],
    number: "",
    numberRules: [
      (v) => !!v || "Phone number is required",
      (v) => (/^[0-9]*$/.test(v) && v.length <= 10) || "number must be valid",
    ],
    query: "",
    queryRules: [
      (v) => !!v || "Query is required",
      (v) => (v && v.length <= 150) || "Query must be less than 100 characters",
    ],
  }),
  methods: {
    async validate() {
      this.$refs.form.validate();
      var data = {
        firstName: this.firstname,
        lastName: this.lastname,
        phoneNumber: this.number,
        email: this.email,
        customerQuery: this.query,
      };
      contactUsService
        .create(data)
        .then((response) => {
          console.log(response.data);
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>
