<template>
  <div>
    <v-container class="pt-15 mt-10">
          <p class="subtitle_text font-weight-bold">
            {{this.getJournalById(this.$route.params.id).title}}
          </p>
        </v-container>
    <v-row class="each_part pb-6">
      <v-col cols="12" md="8">
        <v-card>
          <v-container class="pt-10 pr-10 pl-10 pb-10">
            <h2 style="font-family: 'Castoro', serif;; font-size:38px"> {{this.getJournalById(this.$route.params.id).headline}} </h2>
                        <v-card-text class="grey--text py-5 px-0" style="line-height:5px">
                            <p > By {{this.getJournalById(this.$route.params.id).author}} </p>
                            <span v-text="`${new Date(this.getJournalById(this.$route.params.id).createdAt).getFullYear()}`"/>
                            <span class="primary--text"> | </span>
                            <span v-text="`${new Date(this.getJournalById(this.$route.params.id).createdAt).toLocaleString('default', { month: 'long' })}`"/>
                            <span class="primary--text"> | </span>
                            <span v-text="`${new Date(this.getJournalById(this.$route.params.id).createdAt).getDay()}`"/>
                        </v-card-text>
                    <v-divider class="grey"></v-divider>
                    <v-img class="mt-8" :src="require(`@/assets/`+this.getJournalById(this.$route.params.id).img)"/><br>
                    <markdown-it-vue class="md-body" :content= this.getJournalById(this.$route.params.id).article />
          </v-container>
        </v-card>
      </v-col>
      <v-col cols="12" md="4">
        <v-container class="mt-md-n10">
          <h3 class="text-center">MORE ARTICALS</h3>
          <v-row class="pt-6">
            <v-col
              v-for="journal in getAllJournal.slice(0,3)"
              :key="journal.title"
              style="margin: 0 auto"
            >
              <v-card class="card" :to="`/journald/${journal.journalId}`">
                <v-img :src="require(`@/assets/`+journal.img)"></v-img>
                <v-container>
                  <div>
                    <p
                      class="title primary--text font-weight-bold text-uppercase mt-4"
                    >
                      {{ journal.title }}
                    </p>
                    <p>{{ journal.headline }}</p>
                    <p class="font-weight-bold">{{ journal.date }}</p>
                  </div>
                </v-container>
              </v-card>
            </v-col>
          </v-row>
        </v-container>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import { mapGetters,mapActions } from 'vuex'
import MarkdownItVue from 'markdown-it-vue'
import 'markdown-it-vue/dist/markdown-it-vue.css'

export default {
    name:"journald", 
    components:{
        MarkdownItVue
    },
    methods:{
        ...mapActions(['loadJournal']),
        loadOnce:function(){
            location.reload();
        }
    },
    data:()=>({
        popular :[],
        content: this.getJournalById(this.$route.params.id).article 
    }),
    computed: {
    ...mapGetters([
        'getAllJournal',
        'getJournalById'
    ]),
    },
    watch: {
       route(newv, oldv){
           console.log(`old route is ${oldv}, new route is ${newv}`);
        },
    },
    created(){
        this.$store.dispatch('loadJournal')
    }
}
</script>

