<template>
  <div class="primary white--text pt-6">
    
    <v-row style="width: 90%; margin: 0 auto " >
      <p class="headline font-weight-bold text-uppercase">get started</p>
    </v-row>
      
    <v-row style="width: 90%; margin: 0 auto ">
      <v-col md="4" sm="6" cols="12">
        <p class="title">Email Us</p>
        <p class="blue--text mt-n3">info@quadusk.com</p>
      </v-col>
      <v-col md="4" sm="6" cols="12" lg="4">
        <p class="title">Contact Us</p>
        <p class="mt-n3">(855) 96 651 1876</p>
      </v-col>
      <v-col md="4" lg="4"  cols="12" class="white--text d-flex" >
        <v-text-field
            v-model="email"
            :rules="[rules.required, rules.email]"
            label="EMAIL"
            placeholder="Your business email"
            outlined
            clearable
            color="white"
            background-color="transparent"
            dark
          ></v-text-field>
          <v-btn class="red white--text mt-2 ml-4" @click="validate">subcribe</v-btn>
        </v-col>
    </v-row>
    <v-row class="justify-md-end" style="width: 90%; margin: 0 auto ">
      <v-col cols="12" lg="8">
        <v-container>
          <v-row class="justify-md-end">
          <v-col cols="6" sm="4">
            <p class="text-md-end body-2">QUADUSK , 2020 All right reserved</p>
          </v-col>
          <v-col cols="6" sm="4">
            <p class="text-md-end body-2">PRIVCY POLICY | TERM OF SERVICE</p>
          </v-col>
          <v-col cols="12" sm="4">
            <div class="text-xs-center text-md-end">
              <a href="https://www.facebook.com/quaduskcompany" target="_blank" style="text-decoration: none">
                <v-icon class="white--text">mdi-facebook</v-icon>
              </a>
              <a href="https://www.facebook.com/quaduskcompany" target="_blank" style="text-decoration: none"><v-icon class="white--text pl-4">mdi-facebook-messenger</v-icon></a>
              <v-icon class="white--text pl-4">mdi-twitter</v-icon>
              <a href="https://www.linkedin.com/company/quadusk" target="_blank" style="text-decoration: none"><v-icon class="white--text pl-4">mdi-linkedin</v-icon></a>
              <v-icon class="white--text pl-4">mdi-telegram</v-icon>
            </div>
          </v-col>
        </v-row>
        </v-container>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import notifyCustomerEmailService from '@/services/notifyCustomerEmailService'
export default {
    data: () => ({
        email: '',
        rules: {
          required: value => !!value || 'Required.',
          counter: value => value.length <= 20 || 'Max 20 characters',
          email: value => {
            const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
            return pattern.test(value) || 'Invalid e-mail.'
          },
        },
    }),
    methods:{
      async validate(){
        var data ={
          customerEmail: this.email
        };
        console.log(data)
        notifyCustomerEmailService
        .create(data)
        .then((response) => {
          console.log(response.data);
        })
        .catch((err) => {
          console.log(err);
        });
      }
    }
};
</script>

