import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import RealEstate from '../views/RealEstate.vue'
import Contruction from '../views/Contruction.vue'
import Project from '../views/Project.vue'
import Journal from '../views/Journal.vue'
import About from '../views/About.vue'
import FAQ from '../views/FAQ.vue'
import ProjectDetail from '../views/ProjectDetail.vue'
import JournalD from '../views/JournalDetail.vue'
import Contact from '../views/Contact.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path:'/realestate',
    name: 'Real Eastate',
    component: RealEstate,
  },
  {
    path:'/contruction',
    name: 'Construction',
    component: Contruction,
  },
  {
    path:'/project',
    name: 'Project',
    component: Project,
  },
  {
    path:'/journal',
    name: 'Journal',
    component: Journal,
  },
  {
    path:'/about',
    name: 'About',
    component:About,
  },
  {
    path:'/faq',
    name: 'FAQ',
    component:FAQ,
  },
  {
    path:'/projectdetail',
    name: 'ProjectDetail',
    component:ProjectDetail,
  },
  {
    path: '/journald/:id',
    name: 'journald',
    component: JournalD
  },
  {
    path:'/contact',
    name: 'Contact',
    component: Contact,
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
})

export default router
