<template>
  <div>
    <v-app-bar app color="#008080" dark>
      <v-row color="#008080">
        <v-col class="navbar_container" md="11">
          <v-toolbar class="nav" color="#008080" flat>
            <router-link to="/">
              <div class="d-flex align-center">
              <v-img
                class="shrink mr-2"
                contain
                src="QUADUSK_logo.png"
                transition="scale-transition"
                width="40"
              />
              <v-card class="title white--text primary" flat> QUADUSK </v-card>
            </div>
            </router-link>

            <v-spacer></v-spacer>

            <v-toolbar-items class="nav-links d-none d-md-block">
              <v-btn class="btn" text>
                  <router-link to="/">
                    <span class="mr-2 white--text">HOME</span>
                  </router-link>
              </v-btn>
              <v-btn class="btn" text  @click="$vuetify.goTo(target, type)"  >
                  <router-link to="/">
                    <span class="mr-2 white--text">Service</span>
                  </router-link>
              </v-btn>

              <v-btn class="btn" v-for="item in items" :key="item.id" text :to="item.link">
                <span class="mr-2">{{ item.navText }}</span>
              </v-btn>
            </v-toolbar-items>
          </v-toolbar>
        </v-col>
      </v-row>
      <v-dialog
        v-model="dialog"
        fullscreen
        hide-overlay
        transition="scroll-y-transition"
        style="width:100%; position: fixed"

       
      >
        <template v-slot:activator="{ on }">
          <div>
            <v-btn icon v-on="on" class="d-md-none">
            <v-icon class="" large>mdi-text-short</v-icon>
          </v-btn>
          </div>
        </template>
        <!-- Dialog -->
        <v-row class="primary" width="100%">
          <v-col class="primary">
            <v-toolbar class="pl-5 primary"  flat>
              <div class="d-flex align-center">
                <v-img
                  class="shrink mr-2"
                  contain
                  src="QUADUSK_logo.png"
                  transition="scale-transition"
                  width="40"
                />
                <v-card class="title white--text primary" flat> QUADUSK </v-card>
              </div>

              <v-spacer></v-spacer>

              <v-btn icon @click="dialog = false">
                <v-icon large class="white--text">mdi-close</v-icon>
              </v-btn>
            </v-toolbar>
          </v-col>
        </v-row>

        <v-row width="100%" height="100%" color="#008080" flat>
          <v-row row color="#008080" id="row">
            <v-col class="text-center" style="margin:0 auto; background-color:#008080 " >
              <router-link to="/">
                <v-col class="white--text" @click="dialog = false" >
                <v-flex>
                  HOME
                </v-flex>
              </v-col>
              </router-link>
             
                <router-link to="/">
                  <v-col class="white--text"  @click="$vuetify.goTo(target1, type)" >
                <v-flex @click="dialog = false">
                  SERVICE
                </v-flex>
              </v-col>
                </router-link>
                <v-col v-for="item in items" :key="item.id">
                  <router-link :to="item.link">
                    <v-flex text @click="dialog = !dialog" class=" white--text" >
                    {{ item.navText }}
                  </v-flex>
                  </router-link>
                </v-col>
            </v-col>
          </v-row>
        </v-row>
      </v-dialog>
    </v-app-bar>
  </div>
</template>

<script>
export default {
  data: () => ({
    type: 'number',
    number: 700,
    type1: "number1",
    number1: 350,
    dialog: false,
    items: [
      {
        id: "3",
        link: "/project",
        navText: "PROJECT",
      },
      {
        id: "4",
        link: "/journal",
        navText: "JOURNAL",
      },
      {
        id: "5",
        link: "/faq",
        navText: "FAQ",
      },
      {
        id: "6",
        link: "/about",
        navText: "ABOUT US",
      },
      {
        id: "7",
        link: "/contact",
        navText: "CONTACT US",
      },
    ],
  }),
   computed: {
    target () {
      const value = this[this.type]
      if (!isNaN(value)) return Number(value)
      else return value
    },
    target1 () {
      const value = this[this.type1]
      if (!isNaN(value)) return Number(value)
      else return value
    },
  }
};
</script>

<style scoped lang="scss">
.navbar_container {
  margin: 0 auto;
}

</style>
