import Journal from '@/services/journalService';

const posts = {
	state: () => ({
		journal_content: [
			
		],
		journal_popular: [
			
		]
	}),
	mutations: {
		SET_JOURNAL(state, journals) {
			//console.log(journals[0]);
			state.journal_content = journals;
		}
	},
	actions: {
		async loadJournal({ commit }) {
			let response = await Journal.getJournal().then((response) => {
				return response.data;
			});
			commit('SET_JOURNAL', response);
		}
	},
	getters: {
		getAllJournal(state) {
			//console.log(state.journal_content[0]);
			return state.journal_content;
		},
		getTrendJournal(state) {
			return state.journal_popular;
		},
		getJournalById: (state) => (id) => {
			return state.journal_content.filter((journal) => {
				//console.log(journal)
				return journal.journalId == id;
			})[0];
		}
	}
};
export default posts;
